/* Your own custom utilities */

@font-face {
  font-family: "BandeinsSans";
  src: local("BandeinsSans"),
    url(../fonts/BandeinsSansRegular.otf) format("opentype");
}

body {
  background-color: black;
  background-image: url(../images/background.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

@media (min-width: 768px) {

	.md\:flex {
		display: flex;
	}

	.md\:max-w-2xl {
		max-width: 42rem;
	}

	.md\:flex-none {
		flex: none;
	}

	.md\:p-10 {
		padding: 2.5rem;
	}

	.md\:px-8 {
		padding-left: 2rem;
		padding-right: 2rem;
	}

	.md\:text-5xl {
		font-size: 3rem;
		line-height: 1;
	}

	.md\:text-3xl {
		font-size: 1.875rem;
		line-height: 2.25rem;
	}
}
